<template>
  <div class="dashboard-container">
    <div class="categorys-container">
      <div class="category-container" @click="changeCategory('SET_MANAGEMENT_TO_TASKS')">
        TASKS
      </div>

      <div class="category-container" @click="changeCategory('SET_MANAGEMENT_TO_MATERIALS')">
        MATERIALS
      </div>

      <div class="category-container" @click="changeCategory('SET_MANAGEMENT_TO_PERSONAL')">
        PERSONAL
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "dashboard",
  methods: {
    changeCategory(management_change) {
      this.$store.dispatch(management_change);
      this.$router.push('/management')
    }
  },
  beforeMount() {
    this.$store.commit('SET_NAVBAR_TITLE', 'DASHBOARD')
    this.$store.commit('SET_PREVIUS_PAGE', '/projects')
  }
};
</script>

