      <div class="project-container" v-for="index in 8" :key="index" @click="goToProject">
        PROJECT {{ index }}
      </div>
    </div>
    <div class="buttons-container">
      <button class="projects-buttons">+</button>
      <button class="projects-buttons">-</button>
      <button class="projects-buttons">x</button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "projects",
  methods: {
