export default {
  state: () => ({
    navbar_title: "PROJECTS",
    previus_page: null,
    const_task: [
      { id: 'tasks', label: 'TODO' },
      { id: 'materials', label: 'IN PROCESS' },
      { id: 'personal', label: 'DONE' }
    ],
    const_materials: [
      { id: 'tasks', label: 'ORDERS' },
      { id: 'materials', label: 'IN PROCESS' },
      { id: 'personal', label: 'DONE' }
    ],
    const_personal: [
      { id: 'tasks', label: 'FREE' },
      { id: 'materials', label: 'OCUPIED' },
      { id: 'personal', label: 'NOT AVAILABLE' }
    ]
  }),

  mutations: {
    SET_NAVBAR_TITLE(state, title) {
      state.navbar_title = title;
    },

    SET_PREVIUS_PAGE(state, previus_page) {
      state.previus_page = previus_page;
    }
  },

  actions: {
    SET_MANAGEMENT_TO_TASKS({commit, state}) {
      commit(`management/SET_TABS_LIST`, state.const_task)
      commit(`SET_NAVBAR_TITLE`, 'TASKS')
    },

    SET_MANAGEMENT_TO_MATERIALS({commit, state}) {
      commit(`management/SET_TABS_LIST`, state.const_materials)
      commit(`SET_NAVBAR_TITLE`, 'MATERIALS')
    },

    SET_MANAGEMENT_TO_PERSONAL({commit, state}) {
      commit(`management/SET_TABS_LIST`, state.const_personal)
      commit(`SET_NAVBAR_TITLE`, 'PERSONAL')
    },
  },

  getters: {},
}