export default {
  namespaced: true,

  state: () => ({
    tabs_list: [
      { id: 'tasks', label: 'TODO' },
      { id: 'materials', label: 'IN PROCESS' },
      { id: 'personal', label: 'DONE' }
    ],
    current_tab: 'tasks',
  }),

  mutations: {
    SET_CURRENT_TAB(state, currentTab) {
      state.current_tab = currentTab;
    },

    SET_TABS_LIST(state, tabs_list) {
      state.tabs_list = tabs_list
    }
  },

  actions: {},

  getters: {},
}