<template>
  <nav class="navbar-container">
    <button class="back-button" @click="goBack">
      <i class="fa fa-arrow-left"></i>
    </button>
    <div class="spacerMobile"></div>
    <span class="title">
      {{ navbar_title }}
    </span>
    <div class="spacerMobile"></div>
    <button class="menu-button">
      <i class="fa fa-bars"></i>
    </button>
  </nav>
</template>

<script>

export default {
  name: "Navbar",
  computed: {
    navbar_title() {
      return this.$store.state.root.navbar_title
    }
  },
  methods: {
    goBack() {
      if(this.$store.state.root.previus_page == "/") {
        this.$auth.logout();
        this.$router.push(this.$store.state.root.previus_page)
      } else {
        this.$router.push(this.$store.state.root.previus_page)
      }
    }
  }
};
</script>

